import React from 'react';
import { Icon } from '@iconify/react';
import '../../UI1/Home.css'
import {useState} from 'react'
import { SliderItems as popularProducts } from '../../data'

import MiniProductHolder from '../Product/MiniSideProductForMobile';
export const PostLayout = () => {

   const[isclicked,setClicked]=useState(false);
    const disc='Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolores perspiciatis laboriosam numquam laborum facere vitae facilis, ipsam voluptatum amet aperiam voluptate repellat libero obcaecati iure, debitis praesentium sapiente aliquid nesciunt.'
    return <div>
        <div className="feed">
            <div className="head">
                <div className="user">
                    <img src="data:image/jpeg;base64,"
                        alt="" className="profile-picture" />
                    <div className="info">
                        <h3>Aditi Shinde</h3>
                        <small>Best Electronics project</small>

                    </div>
                </div>
                <span className="edit">
                    <Icon icon="uil:ellipsis-h" />
                </span>
            </div>
            <div style={{padding:'10px'}} className="caption">

{`${disc.substring(0,100)}...`}<a href="#">View</a>
</div>
            <div className="photo">
                <img src="data:image/jpeg;base64,"
                    alt="" className="" />
            </div>
       
            {/* <div className="action-buttons">
                <div className="intteraction-btn">
                    <span><Icon icon="uil:heart" /></span>
                    <span><Icon icon="uil:comment-dots" /></span>
                    <span><Icon icon="uil:share-alt" /></span>

                </div>
                <div className="bookmark">
                    <span><Icon icon="uil:bookmark-full" /></span>

                </div>
            </div> */}

            <div className="liked-by">
                <span>
                    <img src="data:image/jpeg;base64,"
                        alt="" className="" />
                </span>
                <span>
                    <img src="data:image/jpeg;base64,"
                        alt="" className="" />
                </span>
                <span>
                    <img src="data:image/jpeg;base64,"
                        alt="" className="" />
                </span>
                <p>Liked by <b>Sumit</b> and <b>shruti</b> and 360 others</p>

            </div>
            <div>
               
                
            </div>
            <div style={{display:'flex', justifyContent:'space-between', alignItems:'center',alignContent:'center'}}>
                <div style={{padding:'2px 2px', display:'flex', alignItems:'center'}}>

  
            <span style={{fontSize:'38px',margin:'1px 5px'}}><Icon icon="uil:heart" /></span>
            <span style={{fontSize:'38px',margin:'2px 5px'}}><Icon icon="uil:comment-heart" /></span>
            </div>
            <button className='btn btn-primary' style={{padding:'0.5 rem', fontSize:'15px',margin:'2px 5px'}} >Ask for help</button>
           

            </div>
            <div className="text-muted comments ">view 260 comments</div>
          
          
          
           <div onClick={()=>isclicked?setClicked(false):setClicked(true)}>
               <div style={{display:'flex', position:'relative'}}>
                   <h5>Explore Project</h5>
                   <h5 style={{right:'0px',position:'absolute'}}></h5>
               </div>
               {/* <div style={{display:'grid',gridTemplateColumns:' repeat(2,1fr)',rowGap:'10px'}} >
 {/* ---------------message--------------------------------- */}
 {/* {popularProducts.filter((product,index)=>(index<4)).map((product,index)=>(
                                <MiniSideProductForMobile product={product}></MiniSideProductForMobile>
                            ))}
                            
               </div> */} 
               {
                   isclicked?<MiniProductHolder></MiniProductHolder>:<div></div>
               }
               
               
               
           </div>
        </div>






    </div>;
};






